'use client'

import { useEffect, useState } from 'react'
import FormInput from '@/components/inputs/FormInput'
import FormSelect from '@/components/inputs/FormSelect'
import FileUpload from '@/components/inputs/FileUpload'
import { fetchCurrentUser, submitApplication } from '@/utils/api'
import { validateClientApplicationForm } from '@/utils/validators'

const REQUIRED_DOCS = [
  'Account Opening Form',
  'NIN Slip',
  'Undertaken Form',
  'Consent Form',
  'Indemnity Form',
  'Application Letter',
]

type ClientFormData = {
  firstName: string
  surname: string
  otherNames: string
  pfa: string
  rsaBalance: string
  bvn: string
  nin: string
  dob: string
  phone1: string
  phone2: string
  altEmail: string
}

const defaultFormData: ClientFormData = {
  firstName: '',
  surname: '',
  otherNames: '',
  pfa: '',
  rsaBalance: '',
  bvn: '',
  nin: '',
  dob: '',
  phone1: '',
  phone2: '',
  altEmail: '',
}

export default function ClientApplicationForm() {
  const [form, setForm] = useState<ClientFormData>(defaultFormData)
  const [formErrors, setFormErrors] = useState<Record<string, string>>({})
  const [requiredDocs, setRequiredDocs] = useState<Record<string, File | null>>({})
  const [optionalDocs, setOptionalDocs] = useState([{ title: '', file: null as File | null }])
  const [message, setMessage] = useState('')
  const [submitting, setSubmitting] = useState(false)

  useEffect(() => {
    const loadUser = async () => {
      try {
        const user = await fetchCurrentUser()
        setForm((prev) => ({
          ...prev,
          firstName: user.first_name || '',
          surname: user.surname || '',
          otherNames: user.other_names || '',
          pfa: user.pfa || '',
          rsaBalance: user.rsa_balance || '',
          bvn: user.bvn || '',
          nin: user.nin || '',
          dob: user.dob || '',
          phone1: user.phone_number || '',
          phone2: user.alt_phone || '',
          altEmail: user.alt_email || '',
        }))
      } catch {
        window.location.href = '/auth/login'
      }
    }

    loadUser()
  }, [])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({ ...prev, [name]: value }))
  }

  const handleSubmit = async () => {
    setSubmitting(true)
    setMessage('')
    setFormErrors({})

    const errors = validateClientApplicationForm(form)
    if (Object.keys(errors).length > 0) {
      setFormErrors(errors)
      setSubmitting(false)
      return
    }

    try {
      await submitApplication(form, requiredDocs, optionalDocs)
      setMessage('✅ Application submitted successfully!')
    } catch {
      setMessage('❌ Could not submit application.')
    } finally {
      setSubmitting(false)
    }
  }

  return (
    <div className="max-w-4xl mx-auto bg-white shadow rounded-lg p-6">
      <h2 className="text-2xl font-semibold mb-4">Apply for Housing</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <FormInput label="First Name" name="firstName" value={form.firstName} disabled />
        <FormInput label="Surname" name="surname" value={form.surname} disabled />
        <FormInput label="Other Names (Optional)" name="otherNames" value={form.otherNames} disabled />
        <FormSelect label="PFA" name="pfa" value={form.pfa} onChange={handleChange}>
          <option value="">-- Select PFA --</option>
          <option value="ARM Pensions">ARM Pensions</option>
          <option value="Stanbic IBTC">Stanbic IBTC</option>
          <option value="Trustfund Pensions">Trustfund Pensions</option>
        </FormSelect>
        <FormInput
          label="RSA Balance"
          name="rsaBalance"
          type="number"
          value={form.rsaBalance}
          onChange={handleChange}
          error={formErrors.rsaBalance}
        />
        <FormInput
          label="BVN"
          name="bvn"
          value={form.bvn}
          onChange={handleChange}
          error={formErrors.bvn}
        />
        <FormInput
          label="NIN"
          name="nin"
          value={form.nin}
          onChange={handleChange}
          error={formErrors.nin}
        />
        <FormInput
          label="Date of Birth"
          name="dob"
          type="date"
          value={form.dob}
          onChange={handleChange}
          error={formErrors.dob}
        />
        <FormInput
          label="Phone Number"
          name="phone1"
          value={form.phone1}
          onChange={handleChange}
          error={formErrors.phone1}
        />
        <FormInput
          label="Alt Phone Number"
          name="phone2"
          value={form.phone2}
          onChange={handleChange}
        />
        <FormInput
          label="Alt Email"
          name="altEmail"
          type="email"
          value={form.altEmail}
          onChange={handleChange}
          error={formErrors.altEmail}
        />
      </div>

      <hr className="my-6" />
      <FileUpload
        requiredDocs={REQUIRED_DOCS}
        onRequiredChange={setRequiredDocs}
        optionalDocs={optionalDocs}
        onOptionalChange={setOptionalDocs}
      />

      <button
        onClick={handleSubmit}
        disabled={submitting}
        className={`mt-6 w-full py-2 rounded-lg text-white ${
          submitting ? 'bg-gray-400' : 'bg-blue-600 hover:bg-blue-700'
        }`}
      >
        {submitting ? 'Submitting...' : 'Submit Application'}
      </button>

      {message && <p className="mt-4 text-sm text-gray-700">{message}</p>}
    </div>
  )
}
